.board {
    display: flex;
    >*:not(:first-of-type) {
        margin-left: 24px;
    }

    @media(max-width:650px){
        display: flex;
        flex-direction: column;
        >*:not(:first-of-type) {
            margin-left: 14px;
            margin-top: -40px;
        }
        
    }
}

